/* global gapi */
import React, { useCallback, useState, useMemo, useEffect } from 'react';
import { useImmer } from 'use-immer'
import range from 'lodash/range'

import hookWithLocalStorage from '../../utils/hookWithLocalStorage'
import Input from '../../components/Input'
import Box from '../../components/Box'

import context from './context'

const DISCOVERY_DOCS = ["https://sheets.googleapis.com/$discovery/rest?version=v4"];

// Authorization scopes required by the API; multiple scopes can be
// included, separated by spaces.
const SCOPES = "https://www.googleapis.com/auth/spreadsheets.readonly";

const Provider = ({ children }) => {
  const [isSignedIn, updateSigninStatus] = useState()
  const [installed, setInstalled] = useState()
  const [error, setError] = useState()
  const [loading, setLoading] = useState()
  const [spreadsheetUrl, setSpreadsheetUrl] = hookWithLocalStorage('WORKBOOK_URL', '', false)(useState)
  const [sheets, setSheets] = hookWithLocalStorage('WORKSHEETS')(useState)
  const [sheetData, setSheetData] = hookWithLocalStorage('SHEET_DATA', {})(useImmer)

  const initClient = useCallback(() => {
    console.log('begin gapi install')
    gapi.client.init({
      apiKey: process.env.GATSBY_GOOGLE_API_KEY,
      clientId: process.env.GATSBY_GOOGLE_CLIENT_ID,
      discoveryDocs: DISCOVERY_DOCS,
      scope: SCOPES
    }).then(() => {
      // Listen for sign-in state changes.
      gapi.auth2.getAuthInstance().isSignedIn.listen(updateSigninStatus);
      // Handle the initial sign-in state.
      updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
      setInstalled(true)
      console.log('gapi installed')
    }, (err) => {
      setError(err);
      console.error(err)
    });
  }, [])

  useEffect(() => {
    console.log('load gapi script')

    const script = document.createElement('script');  // create a script DOM node
    script.src = 'https://apis.google.com/js/api.js';  // set its src to the provided URL
    script.async = true
    script.defer = true
    script.onload = () => gapi.load('client:auth2', initClient)
    script.onreadystatechange = () => {
      if (script.readyState === 'complete') script.onload()
    }
    document.body.appendChild(script)
  }, [])

  const spreadsheetId = useMemo(() => {
    try {
      const [, id] = /docs.google.com\/spreadsheets\/d\/([^/]+)/.exec(spreadsheetUrl)
      return id
    } catch (e) {
      return ''
    }
  }, [spreadsheetUrl])

  const signIn = useCallback(() => {
    gapi.auth2.getAuthInstance().signIn();
  }, [])

  const signOut = useCallback(() => {
    gapi.auth2.getAuthInstance().signOut();
  }, [])

  const loadSheets = () => {
    if (isSignedIn && !loading && spreadsheetId) {
      console.log('start load sheet')
      setLoading(true)
      const params = {
        // The spreadsheet to request.
        spreadsheetId,  // TODO: Update placeholder value.

        // The ranges to retrieve from the spreadsheet.
        ranges: [],  // TODO: Update placeholder value.

        // True if grid data should be returned.
        // This parameter is ignored if a field mask was set in the request.
        includeGridData: false,  // TODO: Update placeholder value.
      };

      const request = gapi.client.sheets.spreadsheets.get(params);
      request.then((response) => {
        console.log(response.result);
        setSheets(response.result.sheets.map(({ properties }) => properties))
        setLoading(false)
      }, (err) => {
        console.error('error: ' + err.result.error.message);
        setError(err)
        setLoading(false)
      });
    }
  }

  const loadSheetData = useCallback((sheeTitle) => {
    if (isSignedIn && !loading && spreadsheetId && !sheetData[sheeTitle]) {
      // console.log('start load sheet data')
      setLoading(true)
      gapi.client.sheets.spreadsheets.values.get({
        spreadsheetId,
        range: `${sheeTitle}!A1:T`,
      }).then((response) => {
        const { result } = response;
        if (result) {
          setSheetData(draft => {
            draft[sheeTitle] = result.values.map(row => range(20).map(i => row[i] || ''))
          })
        }
        setLoading(false)
      }, (err) => {
        setError(err)
        setLoading(false)
      });
    }
  }, [loading, isSignedIn, sheetData, spreadsheetId])

  const SpreadsheetIdInput = useCallback((p) => (
    <Input
      label="貼上Excel連結"
      value={spreadsheetUrl}
      onChange={e => setSpreadsheetUrl(e.target.value)}
      onKeyUp={e => e.keyCode === 13 && loadSheets()}
      {...p}
    />
  ), [spreadsheetUrl])

  return (
    <context.Provider
      value={{
        isSignedIn,
        signIn,
        signOut,
        loadSheets,
        loadSheetData,
        loading,
        sheets,
        sheetData,
        SpreadsheetIdInput,
        error,
      }}
    >
      {installed ? children : (
        <Box p="1em">Initializing...</Box>
      )}
      {error && (
        <Box p="1em" color="red">{error.stack()}</Box>
      )}
    </context.Provider>
  );
};

export default Provider;
